  src: url("Roboto-Medium.91322885.ttf");
  font-style: normal;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

h1, h2, h3, h4, h5, h6, button, p {
