:root {
  --accent-color-text: #ff6b08;
  --underline-color-active: #ff001b;
  --grey-bg-color: #f7f7f7;
  --footer-text-color: #545454;
  --modal-key-color: #8c8c8c;
  --text-color-black: #000;
  --text-color-white: #fff;
  --bg-color-white: #fff;
  --main-bg-color: #e5e5e5;
  --main-secion-color: #fff;
  --bg-color-white: #fff;
  --btn-bg-color: #ff6b01;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-movie: 350ms cubic-bezier(.4, 0, .2, 1);
  --color-gradient-headers: rgba(0, 0, 0, .56);
}

.back-to-top {
  width: 50px;
  height: 50px;
  background-color: rgba(252, 72, 1, .7);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  display: none;
  position: fixed;
  bottom: 45px;
  right: 15px;
}

.back-to-top:hover, .back-to-top:focus {
  filter: brightness(1.5);
  outline: none;
}

.back-to-top:before, .back-to-top:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.back-to-top:before {
  width: 2px;
  height: 25px;
  background-color: rgba(255, 255, 255, .706);
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.back-to-top:after {
  width: 15px;
  height: 15px;
  border-top: 2px solid rgba(255, 255, 255, .706);
  border-left: 2px solid rgba(255, 255, 255, .706);
  transform: translate(-50%, -75%)rotate(45deg);
}

.dark-theme {
  color: #fff;
  background-color: #3c4242;
}

header.dark-theme {
  box-shadow: 10px 5px 55px red;
}

footer.light-theme {
  background-color: var(--grey-bg-color);
}

footer.dark-theme {
  color: #d3d3d4;
