}

.theme-switch__icon {
  background-image: url("sun.42d68f28.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.theme-switch__icon-moon {
  height: 30px;
  width: 30px;
  background-image: url("moon.55f1ca49.png");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 10px;
}

.theme-switch {
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 4px;
  display: flex;
  position: absolute;
  top: 160px;
  left: 50%;
  transform: translateX(-50%);
}

.theme-switch__icon {
  color: #fff;
  height: 35px;
  width: 35px;
  margin-left: 8px;
  margin-right: 8px;
}

.theme-switch__control {
  width: 64px;
  height: 32px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #333;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 26px;
  height: 26px;
  z-index: 2;
  pointer-events: none;
  transition: transform 250ms var(--timing-function);
  background-color: red;
  border-radius: 50%;
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #d3d3d4;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(35px);
}

.card-set {
  row-gap: var(--gap-top);
  column-gap: var(--gap-left);
  flex-wrap: wrap;
  display: flex;
}

.card-item {
  flex-basis: calc((100% - var(--card-colum) * var(--gap-left) ) / var(--card-item) );
}

